import React, { Component } from 'react'

class Buscador extends Component {

  busquedaRef = React.createRef();// es para captar los datos del input

  obtenerDatos=(e)=>{
    e.preventDefault();
    // console.log(this.busquedaRef.current.value);//revisamos los escrito
    const termino = this.busquedaRef.current.value;
    this.props.datosBusqueda(termino);
  }

  render() {
    return( 
      <form onSubmit={this.obtenerDatos}>
        <div className='row'>
          <div className='form-group col-md-8'>
            <input ref={this.busquedaRef} type='text' className='form-control form-control-lg' placeholder='Busca tu imagen Ejemplo: MTG'/>
          </div>
          <div className='form-group col-md-4'>
            <input type='submit' className='btn btn-lg btn-danger btn-block' value='Buscar...'/>
          </div>
        </div>
      </form>
    );
  }
}

export default Buscador;